.autocomplete-suggestions {
  overflow: auto;
  margin-top: -1px;
  padding: 7px 2px;
  background: #fff;
  border: 1px solid #dfe2e4;
  border-radius: 1px;
  border-top: 0;
  width: 320px !important;
  .autocomplete-suggestion {
    background: #FFF;
    font-size: 12px;
    padding: 0 10px;
    white-space: nowrap;
    overflow: hidden;
    height: 20px;
    line-height: 20px;
    &:hover {
      color: #0d81cb;
      cursor: pointer; } }
  .autocomplete-suggestions strong {
    font-weight: normal; } }

